import { Dispatch, FC, SetStateAction } from 'react'
import s from './burger.module.css'

type BurgerPropsType = {
    openMenu: boolean,
    setOpenMenu: Dispatch<SetStateAction<boolean>>
}

const Burger: FC<BurgerPropsType> = ({ openMenu, setOpenMenu }) => {
    return (
        <div className={s.burger}>
            <svg viewBox="0 0 800 600" className={`${s.svg} ${openMenu ? s.open : ''}`} onClick={() => setOpenMenu(!openMenu)}>
                <path d="M300,220 C300,220 520,220 540,220 C740,220 640,540 520,420 C440,340 300,200 300,200" className={s.topBar} />
                <path d="M300,320 L540,320" className={s.middleBar} />
                <path d="M300,210 C300,210 520,210 540,210 C740,210 640,530 520,410 C440,330 300,190 300,190" className={s.bottomBar} transform="translate(480, 320) scale(1, -1) translate(-480, -318)" />
            </svg>
        </div>
    )
}

export default Burger